import React from "react"
import styles from "./navbar.module.scss";
import { Link } from "gatsby";

const Navbar = () => {

   return (
       <nav className={styles.NavbarContainer}>
         <ui className={styles.NavbarMenu}>
             <li>
                <Link to="/" activeClassName={styles.active }>Posts</Link>
             </li>
             <li>
               <Link to="/videos" activeClassName={styles.active }>Videos</Link>
             </li>
             <li>
               <Link to="/courses" activeClassName={styles.active }>Courses</Link>
             </li>
         </ui>
       </nav>
   );
 };
 
 export default Navbar;