import React from "react"

import styles from "./headerbar.module.scss";

const HeaderBar = ({pageName}) => {

   return (
       <header className={styles.HeaderBarContainer}>
         <h4>{pageName}</h4>
         <ul className={styles.HeaderBaFilters}>
             <li>
                 <a href="#" className={styles.active}>Feed</a>
            </li>
             <li>
                <a href="#">FrontEnd</a>
             </li>
             <li>
                <a href="#">BackEnd</a>
             </li>
             <li>
                <a href="#">Productivity</a>
             </li>
         </ul>
       </header>
   );
 };
 
 export default HeaderBar;