import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { 
    IoLogoTwitter , 
    IoLogoInstagram, 
    IoLogoGithub,
    IoLogoLinkedin,
    IoLogoYoutube
  } from "react-icons/io";

  import styles from "./SocialMediaBar.module.scss";


const query = graphql`
query SocialMediaBarQuery {
  site {
    siteMetadata {
      social {
        twitter
        instagram
        linkedin
        github
        youtube
      }
    }
  }
}
`

const SocialMediaBar = () => {
   const data = useStaticQuery(query);
  const { social } = data.site.siteMetadata;
  return (
      <div className={styles.socialBarContainer}>
        <IoLogoTwitter className={styles.socialBarItems} />
        <IoLogoInstagram className={styles.socialBarItems} />
        <IoLogoGithub className={styles.socialBarItems} />
        <IoLogoLinkedin className={styles.socialBarItems} />
        <IoLogoYoutube className={styles.socialBarItems} />
      </div>
  );
};

export default SocialMediaBar;